}
}

@media only screen and (max-width: 600px) {
  .lol-container {
    margin: 0 15px auto !important;
  }

  .nav-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .nav-bar img {
    width: 25px;
    height: 25px;
  }

  .nav-bar-two a {
    font-size: 10px;
    margin-left: 0;
  }

  .nav-bar-menu {
    display: flex;
    align-items: center;
  }

  .nav-links.show {
    display: flex;
    flex-direction: row;
  }

  .menu-button {
    display: block;
    display: unset !important;
    background-color: transparent;
    border: transparent;
    position: relative;
  }
  /* ___________ */
  .nav-bar-one {
    flex-direction: column;
    align-items: center;
}

.nav-bar img {
    margin-bottom: 10px;
}

.nav-bar-two {
    display: flex;
    /* flex-direction: column; */
    position: absolute;
    /* border-radius: 10px; */
    border: transparent;
    padding: 22px;
    right: -15px;
    top: 100px; 
    background-color:#fbfdfe;
    width:325px !important; 
    box-shadow: -5px 12px 16px 0px rgba(0,0,0,0.2);
    height: auto;
    transition: width 0.1s ease-in-out !important;
    overflow: hidden;
}

.nav-bar-two a {
    font-size: 15px;
    margin: 10px 20px; 
    display: ('${showMenu ? 'block' : 'none'}'); 
}

.menu-button {
    display: block;
    background-color: transparent;
    border: transparent;
    position: relative;
}

.nav-bar-two.active {
  width: 200px;
}

.nav-bar-two {
  width: 0;
  transition: width 0.1s ease-in-out;
  /* overflow: hidden; */
}

/* resume */
.naveen-adjust-resume{
  width: 80%;
  height: 80%;
  margin: auto;
  position: absolute;
  inset: 53px 0px ;
  border: 1px solid rgb(204, 204, 204);
  background: rgb(255, 255, 255);
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;
}
.naveen-resume-newadjust{
  display: grid !important;
}
/* resume */
.nav-bar-two:not(.active) {
  display: none;
}
  /* ________________ */

  .naveen-social {
    display: none !important;
  }

  .naveen-introduce {
    text-align: center;

  }

  .naveen-introduce h2 {
    font-family: poppins !important;
    font-size: 38px !important;
    font-weight: 700 !important;
  }

  .naveen-introduce h3 {
    font-size: 45px !important;
    font-weight: 700 !important;
  }

  .naveen-introduce h5 {
    font-size: 18px !important;
  }

  .naveen-introduce button {
    margin: 0 90px auto;
    margin-bottom: 65px;
  }

  /* about */
  .naveen-about-1 {
    display: grid !important;
    margin: 0 50px auto !important;
    text-align: center;
  }

  .naveen-about-1 img {
    width: 200px !important;
    height: 207px !important;
  }

  .naveen-about h3 {
    margin: 0 0 44px 0 !important;
  }

  .naveen-about-1 h4 {
    text-align: center !important;
    font-size: 16px !important;
    margin: 0 0 35px 0 !important;
  }

  .naveen-resume-button {
    margin: 0 63px auto !important;
  }

  .naveen-qualification {
    padding: 54px 0 !important;
  }

  .footer-naveen {
    margin-top: 85px !important;
  }

  /* skills */
  .naveen-skills {
    grid-template-columns: repeat(1, 1fr) !important;
  }

  /* project */
  .project-card-1 {
    grid-template-columns: repeat(1, 1fr) !important;
  }
  .project-1{
    border-radius: 10px;
  }
  .project-1 img{
    border-radius: 10px 10px 0 0;
    width: 100% !important;
    height:200px !important;
    }
    /* contact */
    .naveen-contact{
      display: grid !important;
    }
    .naveen-form-main{
      display: grid !important;
    }
    .contact-naveen h2 {
      font-size: 32px !important;
    }
}
